.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.app {
  text-align: center;
  margin-top: 50px;
}

h1 {
  color: #333;
}

.input-container {
  margin-bottom: 20px;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}
input {
  padding: 8px;
  margin-right: 10px;
  border-radius: 8px;
}

button {
  padding: 8px 12px;
  border-radius: 5px;
}

.todo-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  margin-bottom: 10px;
}

button {
  margin-left: 10px;
  border-radius: 5px;
}
.container {
  background-color: bisque;
  display: flexbox;
  border-radius: 20px;
  padding: 20px;
}
h5 {
  text-align: right;
}
h1,
.container {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

button:hover {
  border-color: #000000;
}

input:hover {
  border-color: #ffffff;
}
@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  button {
    background-color: #f9f9f9;
  }
  input {
    background-color: #f9f9f9;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
